body {
  height: 100vh;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  /* background-color: lightgray; */
  background: url('./images/background.jpg');
  background-size: 100vw;
}

.board-container {
  height: 80vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; */
}

.title {
  font-family: "Bungee Tint", sans-serif;
  font-size: 42px;
  display: flex;
  justify-content: center;
  align-items: center;

  h1 {
    margin: 10px 0;
  }
}

.square {
  background-color: white;
  width: 250px;
  height: 250px;
  border: 2px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 96px;
  color: rgba(0, 0, 0, 0.8);
  box-shadow: -6px -6px 20px 2px;
  /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;  */
  transition: background-color 0.7s ease;
  
  /* font-family: "Bungee Spice", sans-serif; */
  font-family: 'Macula', sans-serif;
  /* font-family: 'MSU1', sans-serif; */
  /* font-family: 'Mighty Souly', sans-serif; */
  /* font-family: "Bungee Tint", sans-serif; */
}

.square:hover {
  background-color: rgba(255, 0, 0);
  ;
}

.winner-container {
  padding: 22% 0px;
  width: 100%;
  height: 100vh;
  position: fixed;
  display: inline;
  text-align: center;
  font-size: 52px;
  justify-content: center;
  color: orangered;
  transition: background-color 0.8s ease-in-out;
  background-color: rgba(0, 0, 0, 0.7);
}

.winner-message, .next-move {
  font-family: "Bungee Tint", sans-serif;
  margin-top: 400px;
  user-select: none;
  margin-left: 70%;
}

.next-move {
  background-color: transparent;
  display: flex;
  margin-left: 77%;
  margin-top: 24%;
  height: 4%;
  width: 18%;
  padding: 21px 10px;
  align-items: center;
  justify-content: center;
}

.next-move-message {
  font-size: 32px;
}

.restart-button {
  width: 10%;
  height: 50px;
  border: 0px solid transparent;
  border-radius: 20px;
  background-color: lightgreen;
  margin-left: 70%;
  cursor: pointer;
  user-select: none;
  font-size: 24px;
  letter-spacing: 1px;
  transition: border-width 0.3s ease, border-color 0.3s ease, background-color 0.5s ease;
}

.restart-button:hover {
  background-color: greenyellow;
}

.btn {
  color: #00b4d8;
  text-transform: uppercase;
  text-decoration: none;
  border: 2px solid #00b4d8;
  padding: 10px 20px;
  font-size: 17px;
  cursor: pointer;
  font-weight: bold;
  background: transparent;
  position: relative;
  transition: all 0.8s;
  overflow: hidden;
}

.btn:hover {
  background-color: black;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  color: white;
  font-size: 17px;
}

.btn::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 0%;
  top: 0;
  left: -40px;
  transform: skewX(45deg);
  background-color: #00b4d8;
  z-index: -1;
  transition: all 1s;
}

.btn:hover::before {
  width: 160%;
}

.btn:disabled {
  cursor: auto;
  border: 2px solid black;
  color: black;
  background-color: lightgray;
  opacity: 0.8;
  box-shadow: none;
  opacity: 0.4;
}

.btn:disabled::before {
  transform: none;
  left: -2;
  width: 0;
  transition: none;
}

.winner-medal {
  width: 40px;
  height: 40px;
  margin-left: 18px;
  margin-top: 0px;
}

.heart-icon {
  width: 24px;
  height: 24px;
  margin: 0 8px;
}

.span-owner {
  font-size: 24px;
  font-family: "Cedarville Cursive", cursive;
}

.div-owner {
  display: flex;
  justify-content: center;
  align-items: center;
}


@media screen and (max-width: 600px) {
  body {
    background-repeat: space;
    background-size: cover;
    overflow: hidden;
  }

  .title {
    font-size: 22px;
    background-color: #4f000b;
    width: 92%;
    margin-left: 4%;
    margin-top: 2%;
    border-radius: 24px;

    h1 {
      margin: 0;
    }
  }

  .board-container {
    width: 100%;
    height: 100%;
    margin-top: 7%;
  }

  .square {
    width: 110px;
    height: 110px;
    font-size: 54px;
  }

  .winner-container {
    padding: 5px 0;
    margin-top: 135%;
    display: flex;
    flex-wrap: wrap;
    height: 13%;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90%;
    margin-left: 5%;
    border-radius: 28px;
  }

  .winner-message,
  .next-move-message {
    font-family: "Bungee Tint", sans-serif;
    max-width: 62%;
    margin: 0;
    font-size: 32px;
  }

  .restart-button {
    margin: 0px;
    width: 50%;
    height: 40px;
    font-size: 18px;
  }

  .winner-medal {
    width: 40px;
    height: 40px;
    margin-left: 18px;
    margin-top: 0px;
  }

  .heart-icon {
    width: 20px;
    height: 20px;
    margin: 0 8px;
  }

  .span-owner {
    font-size: 20px;
    font-family: "Cedarville Cursive", cursive;
  }

  .div-owner {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    color: black;
  }

  .next-move {
    background-color: transparent;
  }

  .next-move-message {
    font-size: 24px;
  }
}

@media screen and (min-width: 600px) and (max-width:1600px) {
  .title {
    font-size: 24px;
  }

  .board-container {
    width: 100%;
    height: 100%;
    margin-top: 2%;
  }

  .square {
    width: 160px;
    height: 160px;
    font-size: 54px;
  }


}